'use client';

import { useState } from 'react';

interface ProductImageGalleryProps {
    mainImage: string;
    gallery: string[];
    productName: string;
    accentBorderClass: string;
}

export function ProductImageGallery({ mainImage, gallery, productName, accentBorderClass }: ProductImageGalleryProps) {
    const allImages = [mainImage, ...(gallery || [])];
    const [selectedIndex, setSelectedIndex] = useState(0);
    const currentImage = allImages[selectedIndex] || mainImage;

    return (
        <div className="relative">
            {/* Main Image - Responsive sizing */}
            <div className="aspect-square max-h-[300px] sm:max-h-[400px] lg:max-h-[500px] bg-white rounded-2xl sm:rounded-3xl shadow-lg sm:shadow-xl overflow-hidden border border-gray-100 group mx-auto">
                <div className="relative w-full h-full p-4 sm:p-6 lg:p-12 flex items-center justify-center">
                    <img
                        src={currentImage}
                        alt={productName}
                        className="max-w-full max-h-full object-contain transform group-hover:scale-105 transition-transform duration-700 ease-out"
                    />
                </div>
            </div>

            {/* Thumbnail Gallery */}
            {allImages.length > 1 && (
                <div className="mt-3 sm:mt-4 flex gap-2 sm:gap-3 overflow-x-auto pb-2 scrollbar-hide justify-center lg:justify-start">
                    {allImages.slice(0, 4).map((img, i) => (
                        <button
                            key={i}
                            onClick={() => setSelectedIndex(i)}
                            className={`flex-shrink-0 w-14 h-14 sm:w-16 sm:h-16 lg:w-20 lg:h-20 rounded-lg sm:rounded-xl overflow-hidden cursor-pointer transition-all duration-200 ${selectedIndex === i
                                    ? `border-2 ${accentBorderClass} ring-2 ring-offset-1 ring-gray-200`
                                    : 'border border-gray-200 hover:border-gray-400 opacity-70 hover:opacity-100'
                                }`}
                        >
                            <img src={img} alt={`Vue ${i + 1}`} className="w-full h-full object-cover" />
                        </button>
                    ))}
                </div>
            )}
        </div>
    );
}
